// frontend/src/components/ProtectedRoute.jsx
import React, { useContext } from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { AuthContext } from '../contexts/AuthContext';

/**
 * Componente para proteger rutas que requieren autenticacin
 * Si el usuario no est autenticado, lo redirige a la pgina de login
 */
const ProtectedRoute = ({ children }) => {
  const { isAuthenticated, loading } = useContext(AuthContext);
  const location = useLocation();
  
  // Si est cargando, mostrar un indicador de carga
  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>Cargando...</p>
      </div>
    );
  }
  
  // Si no est autenticado, redirigir al login
  if (!isAuthenticated) {
    // Guardar la ubicacin actual para redirigir despus del login
    return <Navigate to="/login" state={{ from: location }} />;
  }
  
  // Si est autenticado, mostrar los children (la ruta protegida)
  return children;
};

export default ProtectedRoute;
